@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto:ital,wght@0,300;0,700;0,900;1,400&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box; }

::-webkit-scrollbar {
  width: 9px; }

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(169, 169, 169, 0.3); }

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(169, 169, 169, 0.4); }

.todo {
  width: 100%;
  height: 100vh;
  display: flex; }
  .todo__sidebar {
    z-index: 199;
    min-width: 230px;
    margin-top: 80px;
    box-shadow: 0px 0px 8px #393939; }
    .todo__sidebar_list_item {
      font-size: 19px;
      margin: 40px 20px;
      font-weight: 300;
      cursor: pointer; }
  .todo .active {
    color: #f24452; }

.navbar {
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  z-index: 200;
  position: fixed;
  flex-wrap: wrap; }
  .navbar__left {
    display: flex; }
  .navbar__data {
    font-size: 21px;
    height: 100%;
    margin-left: 29px;
    margin-right: 25px;
    margin-top: 15px; }
  .navbar__mode-button {
    margin: 27px 0px;
    background: #21b27d;
    border-radius: 20px;
    padding: 5px 9px;
    color: #fff;
    display: inline-block;
    cursor: pointer; }
  .navbar__rigth {
    display: flex; }
  .navbar__money {
    font-size: 35px;
    margin-right: 86px;
    margin-top: 25px; }
  .navbar__person {
    display: flex;
    margin-top: 17px;
    margin-right: 44px; }

.money-icon {
  width: 30px;
  height: 30px; }

.person__photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-left: 23px; }

.logo {
  height: 80px;
  width: 230px;
  background: linear-gradient(45deg, #f2c744, #f2b441, #f24452);
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  font-weight: 800;
  font-size: 40px; }

.content-bar {
  margin-top: 100px;
  text-align: center;
  margin-top: 80px;
  display: flex;
  width: 100%;
  z-index: 0; }
  .content-bar .list-header {
    background: #3b3b3b;
    box-shadow: 0px 0px 8px #393939;
    font-size: 25px;
    color: white;
    font-weight: 500;
    padding: 10px;
    top: 0; }
  .content-bar .list-header-white-theme {
    color: black;
    background: #f1f1f1;
    z-index: 201; }

.work-list {
  width: 50%;
  background: #f6f6f6;
  min-height: 90vh;
  overflow: auto;
  border-right: solid 1px #7e7e7e; }
  .work-list__add-task {
    margin-top: 30px;
    margin-left: 50px;
    margin-right: 50px;
    border-radius: 20px;
    padding: 10px;
    font-size: 18px;
    cursor: pointer;
    background: #e2e2e2; }

.relax-list::-webkit-scrollbar {
  width: 10px; }

.relax-list {
  width: 50%;
  background: #f6f6f6;
  min-height: 90vh;
  overflow: auto; }
  .relax-list__header {
    box-shadow: 3px 3px 8px #b5b5b5;
    background: #fff;
    font-size: 25px;
    font-weight: 500;
    padding: 10px;
    width: 100%;
    position: fixed;
    text-align: center; }

.add-task {
  margin-top: 30px;
  margin-left: 50px;
  margin-right: 50px;
  border-radius: 20px;
  padding: 10px;
  font-size: 18px;
  cursor: pointer;
  background: #e2e2e2; }

.task {
  min-height: 130px;
  box-shadow: 0px 0px 8px #393939;
  background: #fff;
  margin: 60px 85px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left; }
  .task-completed {
    background: #d1e795; }
  .task__down {
    display: flex;
    justify-content: space-between;
    width: 100%; }
  .task__text {
    font-size: 19px;
    padding: 20px; }
  .task__buttons {
    display: flex;
    padding-left: 20px;
    margin-bottom: 10px;
    opacity: 0.8; }
  .task .items-right {
    display: flex; }
  .task__timer {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f24452;
    border-radius: 40px;
    padding: 15px;
    margin-bottom: -20px;
    margin-right: -12px;
    color: #fff;
    cursor: pointer;
    font-weight: 500; }
  .task__price {
    height: 53px;
    width: 53px;
    border-radius: 50%;
    background-color: #393939;
    color: #FFD949;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: -20px;
    margin-right: -10px;
    font-weight: 600;
    cursor: default; }
  .task .price-relax-theme {
    background-color: #FFD949;
    color: black; }

.add-task-modal {
  width: 400px;
  margin: 0 auto; }
  .add-task-modal__input {
    height: 60px;
    width: 400px;
    margin-top: 20px;
    border-radius: 20px;
    outline: none;
    background: #fff;
    font-family: Roboto;
    font-size: 21px;
    border: none;
    box-shadow: inset 0px 0px 3px #393939;
    padding-left: 15px; }
  .add-task-modal__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 10px; }
  .add-task-modal__price {
    display: flex;
    margin: 0px 20px;
    align-items: center; }
    .add-task-modal__price-input {
      margin-left: 10px;
      width: 60px;
      border: #5b5b5b solid 1px;
      border-radius: 10px;
      padding: 3px;
      font-size: 18px; }
  .add-task-modal__set-time {
    padding: 3px 10px;
    background: #ffcf4c;
    border-radius: 20px;
    color: #080808;
    margin-right: 10px;
    cursor: pointer; }
  .add-task-modal__acept {
    background-image: url(/day-plan-app/static/media/tick.3d4eb88d.svg);
    width: 26px;
    height: 26px;
    margin-right: 5px;
    cursor: pointer;
    outline: none;
    border: none;
    opacity: 1; }
  .add-task-modal__acept:hover {
    opacity: 0.8; }
  .add-task-modal__reset {
    background-image: url("https://image.flaticon.com/icons/svg/149/149690.svg");
    width: 25px;
    height: 25px;
    opacity: 0.8;
    cursor: pointer; }
  .add-task-modal__reset:hover {
    opacity: 0.5; }

.icon {
  width: 17px;
  height: 17px;
  margin: 0px 5px;
  cursor: pointer; }

.icon:hover {
  opacity: 0.6; }

.edit-button {
  background-image: url("https://image.flaticon.com/icons/svg/565/565722.svg"); }

.delete-button {
  background-image: url(/day-plan-app/static/media/bin.74de5e29.svg); }

.ready-button {
  background-image: url("https://image.flaticon.com/icons/svg/845/845646.svg");
  width: 20px;
  height: 20px; }

.time-button {
  background-image: url(/day-plan-app/static/media/clock.a9b56864.svg);
  width: 20px;
  height: 20px; }

